
.add-img {
  width: 100px;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  text-align: center;
  position: relative;
  cursor: pointer;
}
.add-img:hover {
  background-color: #f8f8f8;
}
.add-img input {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}



