
.vm-editor {
  background-color: white;
  border-radius: 4px;
  border: 1px solid #eeeff1;
  width: 100%;
}
.vm-editor .global-control {
  position: absolute;
  right: 15px;
}
.vm-editor .vm-editor-html {
  outline: 0;
  min-height: 350px;
  text-align: left;
  padding: 15px;
  font-size: 16px;
}
.vm-editor .vm-editor-html ul,
.vm-editor .vm-editor-html ol {
  margin: 10px 20px;
}
.vm-editor .vm-editor-html ul {
  list-style-type: square;
}
.vm-editor .vm-editor-html ol {
  list-style-type: decimal;
}
.vm-editor .vm-editor-html hr {
  margin: 15px 0;
  border-top: 1px solid #eeeff1;
}
.vm-editor .vm-editor-html pre {
  display: block;
  margin: 10px 0;
  padding: 8px;
  border-radius: 4px;
  background-color: #f2f2f2;
  color: #656565;
  font-size: 14px;
}
.vm-editor .vm-editor-html blockquote {
  display: block;
  border-left: 4px solid #ddd;
  margin: 15px 0;
  padding: 0 15px;
}
.vm-editor .vm-editor-html img {
  margin: 20px 0;
}
.vm-editor .vm-editor-html a {
  color: #41b883;
}



