
.add-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px;
}
.add-link input {
  border: 1px solid #eeeff1;
}
.add-link button {
  margin-left: 10px;
  width: 50px;
  background-color: #555;
  color: white;
  border: none;
  border-radius: 2px;
  outline: 0;
  cursor: pointer;
}



