
button.vm-editor-button {
  width: 24px;
  height: 24px;
  position: relative;
  border-radius: 4px;
  outline: 0;
  cursor: pointer;
  background-color: transparent;
  border: none;
  margin-left: 10px;
}
button.vm-editor-button:first-child {
  margin-left: 0;
}
button.vm-editor-button:hover {
  background-color: #eee;
}
button.vm-editor-button i {
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 24px;
  font-size: 15px;
  color: #858585;
}



