
.vm-editor-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 15px;
  background-color: #fafbfc;
  border-bottom: 1px solid #eeeff1;
  position: relative;
}
.vm-editor-menu .line {
  display: inline-block;
  width: 1px;
  height: 40px;
  margin: 0 10px;
  background-color: #eeeff1;
}
ul.vm-editor-ul li {
  padding: 10px 30px;
}
ul.vm-editor-ul li:hover {
  background: #f8f8f8;
}



